@import url("https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:wght@300&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Tangerine:wght@700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Open+Sans:wght@500&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lobster+Two:ital@1&display=swap");

$quote-font: "Open Sans Condensed", monospace;
$author-font: "Tangerine", serif;
$button-font: "Open Sans", sans-serif;
$credit-font: "Lobster Two", sans-serif;
$quote-size: 24px;
$author-size: 30px;
$button-size: 16px;
$credit-size: 16px;

html,
body {
  min-width: 100%;
  margin: 0px;
  box-sizing: border-box;
}

.full-height {
  width: 100%;
  margin: 0px;
  height: 100vh;  
  display: flex;
  justify-content: center;

  #quote-box {
    align-self: center;
  
    padding: 8%;
    width: 80%;
    max-width: 600px;
    min-height: 350px;
  
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;

    #photo {
      width: 200px;
      height: auto;
    }
    
  }
}

#quote {
  width: 55%;
}

#text {
  text-align: justify;
  font-family: $quote-font;
  font-size: $quote-size;
  margin: 0px;
}
#author {
  text-align: right;
  font-family: $author-font;
  font-size: $author-size;
}



.tweet-newquote {
  position: relative;
  margin: auto;
  width: 100%;

  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

#new-quote {
  font-family: $button-font;
  font-size: $button-size;
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;

  &:hover{
    filter: saturate(2);
  }
}

#tweet-quote{
  svg {
    width: 40px;
    height: 40px;
    &:hover{
      width: 42px;
      height: 42px;

      path{
        filter: saturate(2);
      }
    }
} 
}


#credit {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  width: 40%;
  text-align: center;
  top: 35%;
  font-family: $credit-font;
  font-size: $credit-size;
  a{
    text-decoration: none;
    color: inherit;

    &:hover{
      text-decoration: none;
      filter: saturate(2);
    }
  }
}

@media(max-width: 560px){
  #quote-box{
    width: 100%;
    display: block;
    padding-bottom: 0;
    #photo{
      display: block;
      width: 70%;
      margin: auto;    
    }
    #quote{
      width: 100%;
    }
    .tweet-newquote{
      display: block;
      width: 100%;
      #new-quote{
        display: block;
        margin: auto;
      }
      #tweet-quote{
        display: block;
        width: 100%;
        margin: auto;
  
        svg{
          display: block;
          margin: auto;
        }
      }
      #credit{
        width: 100%;
        height: 2em;
        margin: 2%;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
    
        a{
          margin: 0 0 0 2%;
        }
      }
    }  
  }
  
}
